body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{font-family:"Montserrat",sans-serif;color:#eee;background-color:#1c2025;margin:0;padding:0;box-sizing:border-box}.App{width:100%;text-decoration:none;display:flex;flex-direction:column;justify-content:center;align-items:center}
*{font-family:"Montserrat",sans-serif;color:#eee;background-color:#1c2025;margin:0;padding:0;box-sizing:border-box}.nav{height:10vh;width:100%;max-width:1200px;display:flex;justify-content:space-between;align-items:center;padding:10px 20px}.nav__heading:hover{color:#ffd369}.nav__hamburger:hover{cursor:pointer}.nav__hamburger{height:90%;aspect-ratio:1;display:flex;flex-direction:column;justify-content:space-evenly;align-items:center;background-color:transparent}@media(min-width: 768px){.nav__hamburger{display:none}}.nav__hamburger--line{width:100%;height:3px;background-color:#eee;transition:.3s all ease}
*{font-family:"Montserrat",sans-serif;color:#eee;background-color:#1c2025;margin:0;padding:0;box-sizing:border-box}.nav-mobile{position:fixed;top:0;left:0;right:0;bottom:0;z-index:5}.nav-mobile__links{background-color:#1c2025;color:#eee;height:100%;display:flex;flex-direction:column;justify-content:space-evenly;align-items:center;list-style:none;position:relative}@media(min-width: 768px){.nav-mobile__links{display:flex;justify-content:space-evenly;list-style:none;font-weight:100;width:50%}}.nav-mobile__links li{text-decoration:none}.nav-mobile__cross{height:50px;width:50px;aspect-ratio:1;position:fixed;top:40px;right:20px;background-color:transparent;cursor:pointer}.nav-mobile__cross--line{width:100%;height:3px;background-color:#eee;transition:.3s all ease}.nav-mobile__cross--top{-webkit-transform:rotate(45deg) translateY(80%);transform:rotate(45deg) translateY(80%)}.nav-mobile__cross--middle{display:none}.nav-mobile__cross--bottom{-webkit-transform:rotate(135deg) translateY(50%);transform:rotate(135deg) translateY(50%)}
*{font-family:"Montserrat",sans-serif;color:#eee;background-color:#1c2025;margin:0;padding:0;box-sizing:border-box}.nav-desktop{display:none;width:50%;height:90%}@media(min-width: 768px){.nav-desktop{display:block}}.nav-desktop__links{height:100%;display:flex;justify-content:space-between;align-items:center;color:#eee;font-weight:100;background-color:transparent;list-style:none;padding:10px 0}.nav-desktop__links--item{line-height:10vh}.nav-desktop__links--item:hover{color:#ffd369;border-bottom:3px solid #ffd369}
*{font-family:"Montserrat",sans-serif;color:#eee;background-color:#1c2025;margin:0;padding:0;box-sizing:border-box}.header{height:90vh;width:100%;max-width:1200px;padding:10px;display:flex;flex-direction:column;justify-content:space-evenly}@media(min-width: 768px){.header{padding:30px}}.header__title{font-weight:900;font-size:72px}@media(min-width: 768px){.header__title{font-size:96px}}.header__title--name{color:#ffd369}.header__desc{line-height:1.5;font-weight:100}@media(min-width: 768px){.header__desc{font-size:1.5rem}}.header__button{display:flex;flex-direction:row;justify-content:center;align-items:center;text-decoration:none}
*{font-family:"Montserrat",sans-serif;color:#eee;background-color:#1c2025;margin:0;padding:0;box-sizing:border-box}.work-button{background-color:#ffd369;color:#1c2025;border:none;width:70%;height:10vh;font-size:2rem;border-radius:10px;text-decoration:none}.work-button:hover{cursor:pointer;background-color:#393e46;color:#ffd369}
*{font-family:"Montserrat",sans-serif;color:#eee;background-color:#1c2025;margin:0;padding:0;box-sizing:border-box}.featured-projects{padding:10px}@media(min-width: 1024px){.featured-projects{display:flex;flex-direction:column;align-items:center;justify-content:center}}.featured-projects__intro{width:100%}.featured-projects__intro--title,.featured-projects__intro--desc{padding:10px}.featured-projects__intro--desc{margin-bottom:20px;line-height:1.2}@media(min-width: 768px){.featured-projects__intro--desc{line-height:1.5}}
*{font-family:"Montserrat",sans-serif;color:#eee;background-color:#1c2025;margin:0;padding:0;box-sizing:border-box}.project-card{padding:20px 10px;display:grid;grid-template-rows:repeat(3),auto;border-top:1px solid #eee;max-width:1200px}.project-card__title{margin-bottom:10px}.project-card__buttons{display:flex;justify-content:flex-end;margin-bottom:10px}.project-card__buttons :nth-child(1){margin-left:5px}.project-card__banner{padding:10px;width:100%;background-color:#ffd369;display:flex;flex-direction:column;align-items:center;margin-bottom:10px}@media(min-width: 768px){.project-card__banner{max-height:50vh;flex-direction:row;justify-content:space-evenly;align-items:center;padding:10px}}@media(min-width: 1024px){.project-card__banner{max-height:65vh}}.project-card__banner--img{width:90%;margin-bottom:10px}@media(min-width: 768px){.project-card__banner--img{height:95%;width:auto;margin-bottom:0}}.project-card__banner--desc{background-color:transparent;color:#1c2025;padding:10px;width:90%}@media(min-width: 768px){.project-card__banner--desc{height:95%;margin-left:10px;line-height:1.5}}@media(min-width: 1024px){.project-card__banner--desc{font-size:1.5rem}}.project-card__tech-stack{display:flex;flex-wrap:wrap;align-items:center}.project-card__tech-stack--code{background-color:#393e46;padding:10px 15px;margin-bottom:10px;margin:0 5px 10px 0}@media(min-width: 768px){.project-card__tech-stack--code{margin-right:20px}}@media(min-width: 768px){.project-card__tech-stack{justify-content:flex-start}}
*{font-family:"Montserrat",sans-serif;color:#eee;background-color:#1c2025;margin:0;padding:0;box-sizing:border-box}.code-button{background-color:#393e46;border:none;color:#eee;padding:10px 15px;width:100px}.code-button:hover{background-color:#ffd369;color:#393e46;cursor:pointer}
*{font-family:"Montserrat",sans-serif;color:#eee;background-color:#1c2025;margin:0;padding:0;box-sizing:border-box}.about{height:100vh;width:100%;max-width:1200px;padding:20px 20px;background-color:#1c2025;color:#eee;display:flex;flex-direction:column}.about__intro--title{padding-bottom:10px}.about__intro--hello{margin-bottom:20px;line-height:1.2;font-size:1.2rem}@media(min-width: 768px){.about__intro--hello{line-height:1.5}}.about__profile{border-top:1px solid #eee;width:100%;padding:30px 10px}@media(min-width: 768px){.about__profile{height:50vh;max-height:50vh;display:grid;grid-template-columns:1fr 1fr;grid-template-rows:2fr 1fr;grid-gap:20px;gap:20px}}.about__profile--text{background-color:transparent;margin-top:10px;line-height:1.5rem}.about__profile--photo{width:100%;margin:10px 0}@media(min-width: 768px){.about__profile--photo{height:auto;max-height:95%;width:auto;max-width:100%;margin-bottom:0;grid-row:1/3;grid-column:2/3;justify-self:center}}.about__profile--button{display:flex;justify-content:center;align-items:center}.about__profile--cell{display:flex;align-items:flex-start;justify-content:flex-start}
*{font-family:"Montserrat",sans-serif;color:#eee;background-color:#1c2025;margin:0;padding:0;box-sizing:border-box}.contact{height:90vh;width:100%;max-width:1200px;padding:20px 20px;background-color:#1c2025;color:#eee;display:flex;flex-direction:column}.contact__header{color:#eee;padding:20px}.contact__links{height:70vh;border-top:1px solid #eee;display:flex;flex-direction:column;justify-content:space-evenly;align-items:center}.contact__links--icon:hover{-webkit-filter:opacity(50%);filter:opacity(50%)}.contact__links--button{width:100%;display:flex;justify-content:center}.contact__links--button a{width:100%}
*{font-family:"Montserrat",sans-serif;color:#eee;background-color:#1c2025;margin:0;padding:0;box-sizing:border-box}.skills{height:90vh;width:100%;max-width:1200px;padding:20px;display:flex;flex-direction:column;align-items:center}.skills__intro{border-bottom:1px solid #eee}.skills__intro--title{padding-bottom:20px}.skills__intro--info{padding-bottom:20px}.skills__stack{margin-top:30px;padding-top:20px;width:80%;height:50vh;display:grid;grid-template-columns:1fr 1fr;grid-gap:30px;gap:30px}@media(min-width: 768px){.skills__stack{grid-template-columns:repeat(5, 1fr);grid-template-rows:1fr 1fr;grid-gap:50px;gap:50px}}.skills__stack--cell{display:flex;justify-content:center}.skills__stack--cell img:hover{-webkit-transform:scale(1.5);transform:scale(1.5)}
