.code-button {
  background-color: $color-secondary;
  border: none;
  color: $text-primary;
  padding: 10px 15px;
  width: 100px;
}

.code-button:hover {
  background-color: $color-accent;
  color: $color-secondary;
  cursor: pointer;
}
